export const htmlEntity = {
  methods: {
    decode(str) {
      return str.replace(/&[#039]*;/g, "'")
      .replace(/&[amp]*;/g, '&')
      .replace(/&[quot]*;/g, '"')
      .replace(/&[rsquo]*;/g, '')
      .replace(/&[lsquo]*;/g, '')
      .replace(/&[apos]*;/g, "'")
      .replace(/&[percnt]*;/g, '%')
      .replace(/&[divide]*;/g, '')
      .replace(/&[div]*;/g, '')
      // FIXME: regex matching 'eacute'
      // .replace(/&[aacute]*;/g, '')
      .replace(/&[eacute]*;/g, '')
      .replace(/&[iacute]*;/g, '')
      .replace(/&[ntilde]*;/g, '')
      .replace(/&[oacute]*;/g, '')
      .replace(/&[uacute]*;/g, '')
      .replace(/&[auml]*;/g, '')
      .replace(/&[euml]*;/g, '')
      .replace(/&[iuml]*;/g, '')
      .replace(/&[ouml]*;/g, '')
      .replace(/&[uuml]*;/g, '')
      .replace(/&[yuml]*;/g, '')
      .replace(/&[uuml]*;/g, '')
      .replace(/&[scaron]*;/g, '')
      .replace(/&[sup2]*;/g, '')
    }
  }
}



